import { useEffect, useState } from 'react';
import { RadioGroup, FormControlLabel, Checkbox, MenuItem, InputBase, Select, Radio, LinearProgress, Divider, Typography, Grid, Box, } from '@mui/material';
import { useDispatch, useSelector } from 'src/store';
import { getPreferences, updatePreferences } from 'src/store/slices/setting'
import { Redirect, useLocation } from 'react-router-dom';
import { styled } from '@mui/material/styles';
import useAuth from 'src/hooks/useAuth';
import FormControlWrapper from 'src/components/FormControlWrapper';
import { Formik } from 'formik';
import * as Yup from 'yup';
import FormLabel from 'src/components/FormLabel';
import languages from 'src/assets/json/language.json'
import ProfileLayout from 'src/views/common/ProfileLayout'
import LoadingButton from '@mui/lab/LoadingButton';

const BootstrapInput = styled(InputBase)(({ theme }) => ({
  'label + &': {
    marginTop: theme.spacing(3)
  },
  '& .MuiInputBase-input': {
    marginTop: '6px',
    borderRadius: 4,
    position: 'relative',
    backgroundColor: theme.palette.background.paper,
    border: '1px solid #ced4da',
    fontSize: 16,
    padding: '8px 26px 8px 12px',
    transition: theme.transitions.create(['border-color', 'box-shadow']),
    // Use the system font instead of the default Roboto font.
    fontFamily: [
      '-apple-system',
      'BlinkMacSystemFont',
      '"Segoe UI"',
      'Roboto',
      '"Helvetica Neue"',
      'Arial',
      'sans-serif',
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"'
    ].join(','),
    '&:focus': {
      borderRadius: 4,
      borderColor: '#80bdff',
      boxShadow: '0 0 0 0.2rem rgba(0,123,255,.25)'
    }
  }
}));

const Profile = () => {
  const location = useLocation();
  const dispatch = useDispatch();
  const { theme, preferences, loading } = useSelector((state) => state.setting);

  useEffect(() => {
    dispatch(getPreferences())
  }, [])

  const { isAuthenticated } = useAuth();
  if (isAuthenticated !== null && isAuthenticated === false) {
    const system = location.pathname.split('/')[1];
    const returnUrl = new URLSearchParams(location.search).get('returnUrl');
    return returnUrl ? <Redirect to={returnUrl} /> : <Redirect to={`/${system}/login?returnUrl=/${system}/profile`} />;
  }



  return (
    <ProfileLayout>
      <Typography variant="h6" component="b">
        User Preferences
      </Typography>
      <Divider />
      {loading && <LinearProgress />}
      {preferences &&
        <Formik
          enableReinitialize={false}
          initialValues={preferences}
          validationSchema={Yup.object().shape({})}
          onSubmit={async (values, { setSubmitting }) => {
            dispatch(updatePreferences(values))
          }}
        >
          {({ errors, handleBlur, handleChange, handleSubmit, isSubmitting, touched, values, setFieldValue }) => (
            <Box>
              <Box sx={{ display: 'flex' }}>
                <FormControlWrapper>
                  <FormLabel label="Language" />
                  <Select
                    fullWidth
                    name="lang"
                    input={<BootstrapInput />}
                    defaultValue={preferences?.lang}
                    onChange={handleChange}
                  >
                    {languages
                      ? languages.map((x) => {
                        return <MenuItem value={x.code} key={x.code}>{x.value}</MenuItem>;
                      })
                      : ''}
                  </Select>
                </FormControlWrapper>
                <FormControlWrapper marginLeft={5}>
                  <FormLabel label="Notifications" />
                  <FormControlLabel control={<Checkbox name="notification.asset_transfer" onChange={handleChange} defaultChecked={preferences?.notification?.asset_transfer} />} label="Assets transfer update" />
                </FormControlWrapper>
              </Box>
              <Box>
                <FormControlWrapper>
                  <RadioGroup
                    defaultValue={preferences?.themes}
                    onChange={handleChange}
                    name="themes"
                  >
                    <FormLabel label='Themes' />
                    <FormControlLabel control={<Radio />} label="Dark Mode" value="dark" />
                    <FormControlLabel control={<Radio />} label="Light Mode" value="light" />
                  </RadioGroup>
                </FormControlWrapper>
              </Box>
              <Box>
                <FormControlWrapper>
                  <FormLabel label="Privacy" />
                  <FormControlLabel control={<Checkbox name="privacy.display_email" onChange={handleChange} defaultChecked={preferences?.privacy?.display_email} />} label="Display your email address to other users?" />
                  <br />
                  <FormControlLabel control={<Checkbox name="privacy.display_phone" onChange={handleChange} defaultChecked={preferences?.privacy?.display_phone} />} label="Display your phone number to other users?" />
                </FormControlWrapper>
              </Box>
              <Divider sx={{ mb: 2 }} />
              <LoadingButton variant="outlined" disableElevation type="submit" onClick={handleSubmit} loading={loading}>
                Update
              </LoadingButton>
            </Box>
          )}
        </Formik>
      }
    </ProfileLayout>
  );
};

export default Profile;
